
@import url("https://fonts.googleapis.com/css?family=Roboto|Roboto+Condensed");
body{font-family:'Roboto Condensed', sans-serif;color:#B6A05D;margin:0
}
button{font-family:'Roboto Condensed', sans-serif;border:1px solid #B6A05D;background:none;color:#B6A05D;text-transform:uppercase;padding:15px 20px;letter-spacing:1px
}
button:hover{cursor:pointer
}
button--filled{color:white;background:#B6A05D
}
.overlay{position:absolute;top:0;left:0;bottom:0;right:0;background:black;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center
}
.fade-enter-active,.fade-leave-active{transition:opacity .5s
}
.fade-enter,.fade-leave-active{opacity:0
}
#menu-links-overlay ul{padding:0;margin:0;text-transform:uppercase;text-align:center
}
#menu-links-overlay ul li{list-style:none;font-size:18px;padding-bottom:5px
}
#menu-links-overlay ul li a{color:#B6A05D;text-decoration:none
}
#menu-links-overlay button{border:none
}
#menu-links-overlay button.close{position:absolute;right:10px;top:30px;width:32px;height:32px
}
#menu-links-overlay button.close:before,#menu-links-overlay button.close:after{position:absolute;left:15px;top:0;content:' ';height:33px;width:2px;background-color:#B6A05D
}
#menu-links-overlay button.close:before{transform:rotate(45deg)
}
#menu-links-overlay button.close:after{transform:rotate(-45deg)
}



